/* The cube - where the fun begins :) ===========================*/

/* The cube outer container */
.cube {
  height: 20rem;
  perspective: 60rem;
  width: 20rem;
}

/* The cube's inner container */
/* Note: rotationBox keyframes is placed right at the end */
.cube__container {
  animation: rotationBox 20s linear infinite forwards;
  height: 100%;
  position: relative;
  transform: translateZ(-10rem);
  transform-style: preserve-3d;
  width: 100%;
}

/* This will target every single side of the cubez */
.cube__face {
  align-items: center;
  background-color: transparent;
  display: flex;
  height: 20rem;
  justify-content: center;
  overflow: hidden;
  position: absolute;
  width: 20rem;
  pointer-events: none;
}

/* This will target an individual side of the cube */
.cube__face--front {
  transform: rotateY(0deg) translateZ(10em);
}
.cube__face--back {
  transform: rotateY(180deg) translateZ(10em);
}
.cube__face--left {
  transform: rotateY(-90deg) translateZ(10em);
}
.cube__face--right {
  transform: rotateY(90deg) translateZ(10em);
}
.cube__face--top {
  transform: rotateX(90deg) translateZ(10em);
}
.cube__face--bottom {
  transform: rotateX(-90deg) translateZ(10em);
}


